import { configureStore, getDefaultMiddleware } from '@reduxjs/toolkit'
import { save, load } from 'redux-localstorage-simple'

import application from './application/reducer'
import { updateVersion } from './global/actions'
import user, { UserState } from './user/reducer' // Import UserState type
import transactions from './transactions/reducer'
import swap from './swap/reducer'
import mint from './mint/reducer'
import lists from './lists/reducer'
import burn from './burn/reducer'
import multicall from './multicall/reducer'
import toasts from './toasts'
import { getThemeCache } from '../utils/theme'

type MergedState = {
  user: UserState; // Use the UserState type instead of generic any
  transactions: {
    [key: string]: any;
  };
}

const PERSISTED_KEYS: string[] = ['user', 'transactions']
const loadedState = load({ states: PERSISTED_KEYS }) as MergedState
if (loadedState.user) {
  // Ensure that the required properties are present in loadedState.user
  loadedState.user = {
    ...loadedState.user,
    userDarkMode: getThemeCache(),
    matchesDarkMode: false,
    userExpertMode: false,
    userSlippageTolerance: 0,
    userDeadline: 0,
    tokens: {},
    pairs: {},
    timestamp: 0,
    audioPlay: false,
  };
}

const store = configureStore({
  reducer: {
    application,
    user,
    transactions,
    swap,
    mint,
    burn,
    multicall,
    lists,
    toasts
  },
  middleware: [...getDefaultMiddleware({ thunk: false }), save({ states: PERSISTED_KEYS })],
  preloadedState: loadedState,
})

store.dispatch(updateVersion())

export default store

export type AppState = ReturnType<typeof store.getState>
export type AppDispatch = typeof store.dispatch
